
.search-container{
    display: flex;
    padding-top: 15px;
    justify-content: center;
}

.search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    border-radius: 50px;
    text-align: center;
    background: #a592f2;
    color: #e7e9f0;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.search-input::placeholder{
    color: #e7e9f0;
}

.search-input:focus{
    outline: none;
}